/* Universal Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.4s ease;
}

/* Body Styling */
.bdy {
  font-family: "Josefin Sans", Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-color: white;
  background-image: url("../pics/auth-bg.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

/* Responsive background */
@media (max-width: 1200px) {
  body {
    background-size: contain;
  }
}

/* Fade-in and Fade-out transitions for the entire container */
.fade-in {
  opacity: 0;
  transform: scale(0.95);
  animation: fadeIn 0.4s forwards;
}

.fade-out {
  opacity: 1;
  transform: scale(1);
  animation: fadeOut 0.4s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.95);
  }
}

/* Staggered input animation */
.staggered {
  opacity: 0;
  animation: staggerIn 0.3s forwards;
}

@keyframes staggerIn {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Login Container */
.login-container {
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 40px;
  width: 350px;
  text-align: center;
  backdrop-filter: blur(50px);
}

/* Heading */
.login-form h2 {
  margin-bottom: 20px;
}

/* Input Group */
.input-group {
  margin-bottom: 15px;
  position: relative;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Password Group */
.password-group {
  position: relative;
}

/* Eye Icon */
.eye-icon {
  width: 20px;
  height: 20px;
  background: url("../pics/eye.png") no-repeat center;
  background-size: contain;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 10px;
}

.eye-icon.eye-off {
  background: url("../pics/disabled-eye.png") no-repeat center;
  background-size: contain;
}

/* Remember Me and Forgot Password */
.remember-forgot {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.remember-forgot label,
.remember-forgot a {
  font-size: 14px;
}

/* Sign-In Button */
.sign-in-btn {
  width: 100%;
  padding: 10px;
  background-color: #bd2525;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  position: relative;
  transition: background-color 0.3s ease;
}

.sign-in-btn:hover {
  background-color: #ce2b2b;
}

#sign-in-hover {
  transition: transform 0.3s ease, font-size 0.3s ease;
}

.sign-in-btn:hover #sign-in-hover {
  transform: scale(1.2);
}

/* Checkbox Styling */
#remcheckbox {
  accent-color: #bd2525;
}

/* Forgot password styling */
#forgotpswd {
  color: black;
  text-decoration: none;
}

#forgotpswd:hover {
  color: black;
  text-decoration: underline;
}

/* Login Alternatives */
.login-alternatives {
  margin: 20px 0;
}

.login-alternatives-options {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.google-login,
.apple-login {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 50px;
  background-color: transparent;
  border: 2px solid #bd2525;
  cursor: pointer;
  transition: transform 0.31s ease-in-out, box-shadow 0.31s ease-in-out;
}

.google-login .google-icon,
.apple-login .apple-icon {
  width: 40px;
  height: 40px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.google-login .google-icon {
  background-image: url("../pics/google.png");
}

.apple-login .apple-icon {
  background-image: url("../pics/apple.png");
}

.google-login:hover,
.apple-login:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Sign Up Section */
.sign-up p {
  font-size: 14px;
}

.sign-up a {
  color: #bd2525;
  text-decoration: none;
}

.sign-up a:hover {
  text-decoration: underline;
}
